  <div class="newUser" id="newUser">
     <!--  -->
     <!-- <div @click="User.convertJson()">sdfsdfsdf</div> -->
      <div class="bankCard bgfff icon pl12 bb pr" :class="{jt:this.bankCardInfoTotal.length > 1}" @click="showBank">   
        <div class="dis-inline ver-mid">
        <p></p>
          <img  :src='this.contextPath+"/static/bank-card-stage-static/assets/bank-logo/"+this.bankCardInfo.bankCode+".png"' width="30" height="30" alt="banklogo">
        </div>
        <div class="dis-inline ver-mid">
          <span class="fs16 c333">{{bankCardInfo.bankName}} </span>
        </div>
      </div>

      <div class="bankCard bgfff icon pl12 bb pr" :class="{jt:this.periodList[this.bankCardInfoTotal[0].bankCode].length > 1}"  @click="showStage">
        <div class="dis-inline fs16 stagetext">
          <span class="fs16 c666"></span>
        </div>
        <span class="stageNo fs16 c666">{{this.bankStage}}</span>
      </div>
      <!--  -->
      <ul class="info bgfff pl12 c666 pt10 pb10">
          <li class="li">
            <span class="text">:</span>&nbsp;<i class="ver-t"></i><i class="orderMoney">{{this.orderAmount}}</i>
          </li>
          <li class="li">
            <span class="text">:</span>&nbsp;<i class="ver-t"></i><i class="handleMoney">{{this.fee}}</i>
          </li>
           <li class="li" v-show="this.feeAfterSubsidy">
            <span class="text">:</span>&nbsp;<i class="ver-t cff491f"></i><i class="handleMoney cff491f">{{this.feeAfterSubsidy}}</i>
          </li>
          <li class="li">
            <span class="text">:</span>&nbsp;<i class="ver-t"></i><i class="firstMoney">{{this.firstPayment}}</i>
          </li>
           <li class="li">
            <span class="text">:</span>&nbsp;<i class="ver-t"></i><i class="firstMoney">{{this.terminalPayment}}</i>
          </li>
          <li class="fs12 cred mt10"></li>
        </ul>
      <!--  -->
      <p class="fs12 c999 mt20 pl12"></p>
       <form class="mt10 bgfff" id="form" @submit="noSubmit($event)">
        <label>
          <div class="li pr pl12 pr12 fs16 w borderbox">
              <input class="required cardNo input-text " name="cardno" _name="cardno" type="number" placeholder="" v-model="cardNo">
              <i class="del pa icon" style="top:10px;" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
      </form>
      <div class="mt20 pl12">
        <label for="agreement">
            <input class="hide" id="agreement" type="checkbox" :checked="protocal" v-model="protocal">
            <i class="icon agreement"></i>
        </label><a class="c2eb944 pr protocal" @click="showProtocal" ></a>
      </div>
      <div class="pl12 pr12 mt30">
        <button class="w" id="submit" type="button" @click="next" :disabled="!protocal">
          <i></i>
        </button>
        <!-- <router-link class="mt30 w" tag="button" to="1.html"></router-link> -->
      </div>
        <!--  -->
      <div class="bank-tip" v-show="isShowBankTip">
        <h2></h2>
        <div class="bt pt10 pb10 li" v-for="(item,index) of this.bankCardInfoTotal" :class='{bgf5f5f5:item.status=="unusable"}' @click="changeBank(item)">
          <div class="dis-inline ver-mid pl12">
            <img  :src='this.contextPath+"/static/bank-card-stage-static/assets/bank-logo/"+item.bankCode+".png"' width="30" height="30" alt="banklogo">
          </div>
          <div class="dis-inline ver-mid ml6">
