          <p class="sno bt" @click="changeStage(no)" v-for="no of  this.periodList[this.bankCardInfo.bankCode]">{{no}}</p>
        </div>
      </div>
      <!--  -->
      <!--  -->
      <div class="verify bgfff tc " v-show="isShowVerify">
        <p class="tc h40 lh40 c333 fs16 bb"> <i class="icon pa fs24 close" @click="close">&#xe60e;</i></p>
